.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0; }

.sr-only-focusable:active, .sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto; }

.row {
  display: flex;
  flex-wrap: wrap;
  margin-right: -10px;
  margin-left: -10px; }

.col-6 {
  position: relative;
  width: 100%;
  min-height: 1px;
  padding-right: 10px;
  padding-left: 10px;
  box-sizing: border-box; }

#quest-post-tiny-body .fs-label-wrap, #quest-post-tiny-body .fs-dropdown {
  border: 1px solid #ccc;
  width: 100%; }

#quest-post-tiny-body .fs-search {
  border-bottom: 1px solid #ccc; }

#quest-post-tiny-body .fs-dropdown {
  background-color: #fff;
  position: absolute; }
  #quest-post-tiny-body .fs-dropdown .fs-option:hover {
    background-color: #007fff; }
    #quest-post-tiny-body .fs-dropdown .fs-option:hover .fs-option-label {
      color: #fff; }
      #quest-post-tiny-body .fs-dropdown .fs-option:hover .fs-option-label i {
        color: #fff; }

.col-6 {
  flex: 0 0 50%;
  max-width: 50%; }

.resource-preview .resource-group {
  cursor: move; }
  .resource-preview .resource-group label {
    font-weight: bold;
    margin-bottom: 6px;
    display: block;
    cursor: pointer; }
    .resource-preview .resource-group label::before {
      border-left: .4em solid transparent;
      border-bottom: 0;
      border-right: 0.4em solid transparent;
      border-top: .4em solid;
      content: " ";
      display: block;
      float: right;
      margin-top: 2px; }
  .resource-preview .resource-group.collapsed label::before {
    border-left: .4em solid;
    border-bottom: .4em solid transparent;
    border-right: 0;
    border-top: .4em solid transparent;
    margin-right: 2px; }
  .resource-preview .resource-group.collapsed ul {
    display: none; }

.resource-preview .resource-group-highlight, .resource-preview .resource-item-highlight {
  border: 1px dashed #0085ba;
  background-color: lightyellow;
  display: block;
  height: 3em;
  box-sizing: border-box; }

.resource-preview .resource-item-highlight {
  background-color: lightgrey;
  height: 1.4em; }

.media-modal {
  z-index: 99999999 !important; }

.quest-media-shortcode .screenshot {
  text-align: center; }
  .quest-media-shortcode .screenshot img {
    width: 80% !important;
    max-height: 150px; }

.thumbnail-pdf-preview {
  width: 120px;
  box-shadow: inset 0 0 15px rgba(0, 0, 0, 0.1), inset 0 0 0 1px rgba(0, 0, 0, 0.05);
  background: #eee;
  cursor: pointer;
  display: inline-block;
  padding: 10px;
  text-align: center;
  margin-bottom: 1rem;
  margin-right: 1rem; }
  .thumbnail-pdf-preview i {
    font-size: 5rem; }
  .thumbnail-pdf-preview label {
    display: block;
    height: 2.7em;
    overflow: hidden;
    word-break: break-all;
    padding-top: 0.5rem; }

.resource-del-pdf {
  width: 140px;
  text-align: center; }

